import React from 'react';
import { connect } from '@xweb/xweb-next/redux';
import styles from './IndexPage.less';

function IndexPage() {
  return (
    <div className={styles.normal}>
      <h1 className={styles.title}>Hello World!</h1>
    </div>
  );
}

IndexPage.propTypes = {
};

export default connect()(IndexPage);



